import React, { useState } from "react";
import { ethers } from "ethers";
import { motion } from "framer-motion";
import "./App.css"; 

const contractAddress = "0x629455E738423F7DF83067a36518f521Ca3DBf49";  
const contractABI = [
  {
    "inputs": [
      { "internalType": "address", "name": "to", "type": "address" }
    ],
    "name": "mintNFT",
    "outputs": [],
    "stateMutability": "nonpayable",
    "type": "function"
  }
];

function App() {
  const [account, setAccount] = useState(null);
  const [loading, setLoading] = useState(false);

  const connectMetamask = async () => {
    if (window.ethereum) {
      const provider = new ethers.BrowserProvider(window.ethereum); // 修改這裡
      const signer = await provider.getSigner();
      const userAccount = await signer.getAddress();
      setAccount(userAccount);
    } else {
      alert("請安裝 Metamask！");
    }
  };

  const mintNFT = async () => {
    if (!account) {
      alert("請先連接 Metamask！🎀");
      return;
    }

    setLoading(true);

    try {
      const provider = new ethers.BrowserProvider(window.ethereum); // 修改這裡
      const signer = await provider.getSigner();
      const contract = new ethers.Contract(contractAddress, contractABI, signer);
      const tx = await contract.mintNFT(account);
      await tx.wait();
      alert("✨ NFT 鑄造成功！🎉");
    } catch (error) {
      console.error("錯誤:", error);
      alert("❌ 鑄造 NFT 失敗！");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="App">
      <h1 className="title">🎀 NFT 鑄造平台 🎀</h1>

      {account ? (
        <p className="account-text">已連接帳戶：{account}</p>
      ) : (
        <motion.button 
          className="cute-button" 
          onClick={connectMetamask}
          whileHover={{ scale: 1.1 }} 
          whileTap={{ scale: 0.9 }}
        >
          🚀 連接 Metamask
        </motion.button>
      )}

      <div className="button-container">
        <motion.button 
          className="cute-button mint-button" 
          onClick={mintNFT} 
          disabled={loading}
          whileHover={{ scale: 1.1 }} 
          whileTap={{ scale: 0.9 }}
        >
          {loading ? "⏳ 鑄造中..." : "✨ 鑄造 NFT"}
        </motion.button>
      </div>

      {loading && <div className="loading-spinner"></div>}
    </div>
  );
}

export default App;